'use client'

import React, { useState, useEffect } from "react";
import { Button, Row, Col } from "reactstrap";
import fr from "date-fns/locale/fr";
import en from "date-fns/locale/en-GB";
import ptBR from "date-fns/locale/pt-BR";
import { useStateMachine } from "little-state-machine";
import { updateSearch, SearchState } from "../../utils/updateActions";
import { useRouter } from "next/navigation";
import axios from "axios";
import { toast } from "react-hot-toast";
import "react-datepicker/dist/react-datepicker.css";
import DatePicker from "react-datepicker";
import { useTranslation } from "react-i18next";
import moment from "moment";
import Select, { StylesConfig } from 'react-select';
import { useForm } from "react-hook-form";
import type { Trip } from "../../interfaces/trips.interface";

interface SearchProps {
  from?: string;
  text?: string;
  setData?: (data: Trip[]) => void;
}

interface TownOption {
  label: string;
  value: string;
}

interface SearchFormData {
  from?: string;
  to?: string;
  journeyDate?: Date;
}

const towns: TownOption[] = [
  { label: 'Bafoussam', value: 'Bafoussam' },
  { label: 'Bamenda', value: 'Bamenda' },
  { label: 'Buea', value: 'Buea' },
  { label: 'Douala', value: 'Douala' },
  { label: 'Dschang', value: 'Dschang' },
  { label: 'Edea', value: 'Edea' },
  { label: 'Garoua', value: 'Garoua' },
  { label: 'Kribi', value: 'kribi' },
  { label: 'Kumba', value: 'Kumba' },
  { label: 'Limbe', value: 'Limbe' },
  { label: 'Maroua', value: 'Maroua' },
  { label: 'Nkongsamba', value: 'Nkongsamba' },
  { label: 'Yaounde', value: 'Yaounde' },
];

const Search: React.FC<SearchProps> = ({ from, text, setData }) => {
  const { t } = useTranslation();
  const router = useRouter();
  const [startDate, setStartDate] = useState<Date>(new Date());
  const [depDate, setDepDate] = useState<Date>(new Date());
  const [toCity, setToCity] = useState<TownOption | null>(towns.find(t => t.value === 'Douala') || null);
  const [selectedOption, setSelectedOption] = useState<TownOption | null>(towns.find(t => t.value === 'Yaounde') || null);
  const [language, setLanguage] = useState<string>('eng');

  const {
    getValues,
    handleSubmit,
  } = useForm<SearchFormData>();

  const { actions } = useStateMachine({ updateSearch });

  useEffect(() => {
    if (typeof window !== 'undefined') {
      const key = localStorage.getItem('i18nextLng');
      setLanguage(key || 'eng');
    }
  }, []);

  const formData: SearchState = {
    from: selectedOption?.value,
    to: toCity?.value,
    journeyDate: moment(startDate).format('YYYY-MM-DD'),
    departureTime: moment(depDate).format('HH:mm'),
  };

  const searchTrip = async (): Promise<void> => {
    actions.updateSearch(formData);
    if (!formData.from || !formData.to || !formData.journeyDate || !formData.departureTime) {
      toast.error('All fields are required');
      return;
    }
    router.push("/trip-search");
  };

  const searchAnotherTrip = async (): Promise<void> => {
    actions.updateSearch(formData);
    const fromValue = getValues("from");
    const toValue = getValues("to");
    const dateValue = getValues("journeyDate");
    
    try {
      const { data } = await axios.get<{ error: boolean; message: string; data?: Trip[] }>(`api/v1/trips/trip-search`, {
        params: { from: fromValue, to: toValue, journeyDate: dateValue }
      });
      if (data?.error) {
        toast.error(data.message);
      } else if (data.data && setData) {
        setData(data.data);
      }
    } catch (err: unknown) {
      if (axios.isAxiosError(err)) {
        toast.error(err.message || 'Search failed');
      } else {
        toast.error('An unexpected error occurred');
      }
    }
  };

  const customStyles: StylesConfig<TownOption, false> = {
    option: (defaultStyles, state) => ({
      ...defaultStyles,
      color: state.isSelected ? "#fff" : state.isFocused ? '#fff' : '#000',
      backgroundColor: state.isSelected ? "#bcc015" : state.isFocused ? '#bcc015' : '#fff',
      ':active': {
        ...defaultStyles[':active'],
        backgroundColor: state.isSelected ? '#162f27' : state.isFocused ? '#bcc015' : '#162f27',
      },
    }),
    control: (defaultStyles) => ({
      ...defaultStyles,
      backgroundColor: "#fff",
      borderColor: '#ccc',
      boxShadow: "#bcc015",
      ':hover': {
        ...defaultStyles[':hover'],
        borderColor: '#bcc015',
      },
    }),
    singleValue: (defaultStyles) => ({ ...defaultStyles, color: "#000" }),
  };

  return (
    <form
      className="input_style_2 d-flex flex-lg-column justify-content-center align-items-center"
      onSubmit={text === 'book' ? handleSubmit(searchTrip) : handleSubmit(searchAnotherTrip)}
    >
      <Row>
        <Col lg={12} className="mb-4">
          <span>{t('from')}</span>
          <Select<TownOption, false>
            options={towns}
            onChange={(option) => setSelectedOption(option)}
            placeholder={selectedOption?.label}
            styles={customStyles}
            isSearchable
            defaultValue={from ? towns.find(t => t.value === from) : selectedOption}
            value={selectedOption}
          />
        </Col>

        <Col lg={12} className="mb-4">
          <span>{t('to')}</span>
          <Select<TownOption, false>
            options={towns}
            onChange={(option) => setToCity(option)}
            placeholder={toCity?.label}
            styles={customStyles}
            isSearchable
            defaultValue={toCity}
            value={toCity}
          />
        </Col>
        {text === 'book' ? (
          <Col lg={12} className="mb-4">
            <span>{t('journey_date')}</span>
            <DatePicker
              selected={startDate}
              onChange={(date: Date | null) => date && setStartDate(date)}
              minDate={moment().toDate()}
              locale={language === 'fr' ? fr : en}
              placeholderText={moment(startDate).format('MM-DD-YYYY')}
              className="form-control"
            />
          </Col>
        ) : (
          <Col lg={12}>
            <span>{t('journey_date')}</span>
            <DatePicker
              selected={startDate}
              onChange={(date: Date | null) => date && setStartDate(date)}
              minDate={moment().toDate()}
              locale={language === 'fr' ? fr : en}
              className="form-control"
              placeholderText={moment(startDate).format('MM-DD-YYYY')}
            />
          </Col>
        )}
        <Col lg={12} className="mb-4">
          <span>{t('time')}</span>
          <DatePicker
            selected={depDate}
            onChange={(date: Date | null) => date && setDepDate(date)}
            minDate={moment().toDate()}
            placeholderText={moment(depDate).format('HH:mm a')}
            showTimeSelect
            showTimeSelectOnly
            timeIntervals={60}
            timeCaption="Time"
            locale={ptBR}
            dateFormat="p"
            timeFormat="p"
            className="form-control"
          />
        </Col>

        <Col lg={12}>
          <div className="form-group">
            {text === 'book' ? (
              <Button block type="submit" color="primary">
                {t("search")}
              </Button>
            ) : (
              <Button block type="submit" color="primary">
                {t("modify_search")}
              </Button>
            )}
          </div>
        </Col>
      </Row>
    </form>
  );
};

export default Search;
